import React, { useState } from 'react'

function TestPage() {

  //
  const [items, setItems] = useState<number[]>([])

  //
  const handleClick = () => {
    for (let i = 0; i < 5; i++) {
      setItems((oldValue => [...oldValue, i]))   // 
    }
  }

  return (
    <div>
      <button onClick={handleClick}></button>
      <ul>
        {items.map(value => <li key={value}>value={value}</li>)}
      </ul>
    </div>
  )
}

export default TestPage